import React, { useEffect, useState } from "react";
import HeaderProduct from "./HeaderProduct";
import mockImg from "../../assets/icons/mockImg.png";
import Constructor from "./Constructor";
import BottomBar from "../BottomBar/BottomBar";
import { useNavigate, useSearchParams } from "react-router-dom";
import { mockData } from "../../utils/mockData";
import Description from "./Description";
import { useSelector } from "react-redux";
import axios from "axios";
import LoaderBlack from "../../utils/LoaderBlack";

const Product = ({ windowWidth }) => {
  const { pwa } = useSelector((state) => state.appReducer);
  const cof = windowWidth / 390;
  let [searchParams, setSearchParams] = useSearchParams();
  const navigate = useNavigate();
  const [colors, setColors] = useState([]);
  console.log("test");
  const [menu, setMenu] = useState([
    {
      title: "Description",
      active: true,
    },
    {
      title: "Color",
      active: false,
    },
    {
      title: "Reviews",
      active: false,
    },
  ]);
  const [data, setData] = useState(false);
  const [allData, setAllData] = useState();

  const getVar = (data, color) => {
    console.log(" ~ file: Product.js:37 ~ getVar ~ data:", data);
    console.log(" ~ file: Product.js:37 ~ getVar ~ color:", color);
    const filter = data.filter((el) => el.name.indexOf(color) !== -1);
    console.log(" ~ file: Product.js:39 ~ getVar ~ filter:", filter);
    const arraySizes = filter.map((item, key) => {
      console.log(" ~ file: Product.js:39 ~ arraySizes ~ item:", item.files);
      const split = item.sku.split("_");
      const titleSize = split[1].split("-");

      if (key === 0) {
        return {
          ...item,
          title: titleSize[titleSize.length - 1],
          active: true,
        };
      } else {
        return {
          ...item,
          title: titleSize[titleSize.length - 1],
          active: false,
        };
      }
    });
    return { arraySizes: arraySizes, img: filter[0].files[1].preview_url };
  };
  const getColors = async (prodId) => {
    try {
      const item = await axios.get(
        `https://stingray-app-lodnw.ondigitalocean.app/https://api.printful.com/products/${prodId[0].product.product_id}`
      );

      const array = item.data.result.variants.map((i) => {
        return { color: i.color_code, title: i.color };
      });
      const uniqueArray = Array.from(new Set(array.map(JSON.stringify))).map(
        JSON.parse
      );
      const indexBlack = uniqueArray.findIndex((el) => el.title === "Black");
      const colorsArray = uniqueArray.map((i, key) => {
        if (key === indexBlack) {
          return { ...i, active: true };
        } else {
          return { ...i, active: false };
        }
      });
      setColors(colorsArray);
    } catch (err) {
      console.log(err);
    }
  };
  const changeColor = (item, index) => {
    let newArray = colors.map((item) => {
      return { ...item, active: false };
    });
    newArray[index].active = true;
    const { arraySizes, img } = getVar(allData.sync_variants, item.title);
    setData({
      img: img,
      price: allData.sync_variants[0].retail_price,
      variants: arraySizes,
      color: item.title,
      id: allData.sync_product.id,
    });
    setColors(newArray);
  };
  const getData = async (id) => {
    const axiosConfig = {
      headers: {
        Authorization: `Bearer A5tPuY7OMklHgY6cCVOZYHCZbpViehKusA9ITEcH`,
      },
    };

    try {
      const item = await axios.get(
        `https://stingray-app-lodnw.ondigitalocean.app/https://api.printful.com/store/products/${id}`,
        axiosConfig
      );
      if (item.data.result.sync_variants.length > 1) {
        getDataTshirt(item);
      } else {
        getDataMug(item);
      }
      console.log(" ~ file: Product.js:114 ~ getData ~ item:", item);
    } catch (err) {
      console.log(err);
    }
  };
  const getDataTshirt = async (item) => {
    try {
      setAllData(item.data.result);
      getColors(item.data.result.sync_variants);
      const { arraySizes, img } = getVar(
        item.data.result.sync_variants,
        "Black"
      );
      setData({
        img: img,
        price: item.data.result.sync_variants[0].retail_price,
        variants: arraySizes,
        color: "Black",
        id: item.data.result.sync_product.id,
      });
    } catch (err) {
      console.log(err);
    }
  };
  const getDataMug = (item) => {
    setData({
      img: item.data.result.sync_variants[0].files[1].preview_url,
      price: item.data.result.sync_variants[0].retail_price,
      variants: [{ ...item.data.result.sync_variants[0], active: true }],
      color: false,
      id: item.data.result.sync_product.id,
    });
  };
  useEffect(() => {
    const product_id = searchParams.get("product_id");
    const category = searchParams.get("category");
    if (category === "t_shirts") {
      getData(product_id);
      setData(false);
    } else {
      const categoryData = mockData[category];
      const productData = categoryData.filter((el) => el.id === product_id);

      setData(productData[0]);
    }
  }, [setData]);
  const setSize = (index) => {
    console.log(index);
    let newData = { ...data };
    let newArray = data.variants.map((item) => {
      return { ...item, active: false };
    });
    newArray[index].active = true;
    newData.variants = newArray;
    setData(newData);
  };

  const changeMenu = (index) => {
    let newArray = menu.map((item) => {
      return { ...item, active: false };
    });
    newArray[index].active = true;
    setMenu(newArray);
  };

  function compareObjects(obj1) {
    //  ,      
    const { count, link, ...restObj1 } = obj1;

    //    
    return JSON.stringify(restObj1);
  }

  const addToCart = () => {
    const cartData = localStorage.getItem("cartData");
    let cartDataArray = JSON.parse(cartData);
    console.log(
      " ~ file: Product.js:109 ~ addToCart ~ cartDataArray:",
      cartDataArray
    );
    const category = searchParams.get("category");
    let dataToCart;
    if (category === "t_shirts") {
      const filterSize = data.variants.filter((el) => el.active);
      const size = filterSize[0].title;
      dataToCart = {
        ...data,
        size: size,
        color: data.color,
        category: category,
        link: window.location.href,
        count: 1,
        price: getPrice(data),
      };
    } else {
      dataToCart = {
        ...data,
        category: category,
        link: window.location.href,
        count: 1,
      };
      console.log(dataToCart);
    }

    if (cartDataArray) {
      const filterObj = cartDataArray.filter(
        (el) => compareObjects(el) === compareObjects(dataToCart)
      );

      if (filterObj.length !== 0) {
        const index = cartDataArray.findIndex(
          (el) => compareObjects(el) === compareObjects(dataToCart)
        );
        cartDataArray[index].count = cartDataArray[index].count + 1;
      } else {
        cartDataArray = [...cartDataArray, dataToCart];
      }
    } else {
      cartDataArray = [dataToCart];
    }
    localStorage.setItem("cartData", JSON.stringify(cartDataArray));
    navigate("/cart");
  };
  const getPrice = (data) => {
    console.log(" ~ file: Product.js:243 ~ getPrice ~ data:", data);

    const filter = data.variants.filter((el) => el.active);
    console.log(" ~ file: Product.js:220 ~ getPrice ~ filter:", filter);
    return filter[0].retail_price;
  };
  return (
    <>
      {data ? (
        <div style={{ background: "#FCFCFC" }}>
          <HeaderProduct cof={cof} />
          <div
            style={{
              position: "relative",
              width: 280 * cof,
              marginLeft: 56 * cof,
              marginTop: 20 * cof,
            }}
          >
            <img
              alt="product"
              src={data?.img}
              style={{
                width: 280 * cof,
                // height: 193 * cof,
                objectFit: "cover",
                borderRadius: 10,
              }}
            />
            <div
              style={{
                width: 65 * cof,
                height: 25 * cof,
                borderRadius: 10 * cof,
                backgroundColor: "#B6ED7F",
                boxShadow: "0px 4px 4px 0px rgba(0, 0, 0, 0.10) inset",
                fontSize: 12 * cof,
                fontWeight: 500,
                display: "flex",
                justifyContent: "center",
                color: "#fff",
                alignItems: "center",
                position: "absolute",

                left: 13 * cof,
                bottom: 11 * cof,
              }}
            >
              ${getPrice(data)}
            </div>
          </div>
          {searchParams.get("category") === "t_shirts" &&
          data?.variants.length > 1 ? (
            <Constructor
              cof={cof}
              sizes={data?.variants}
              setSize={setSize}
              menu={menu}
              changeMenu={changeMenu}
              pwa={pwa}
              colors={colors}
              changeColor={changeColor}
            />
          ) : (
            <div style={{ marginTop: 20 * cof }}>
              <Description cof={cof} data={data} />
            </div>
          )}

          <div
            style={{
              width: "100%",
              display: "flex",
              justifyContent: "center",
              position: "absolute",
              bottom: pwa ? 146 * cof : 25 * cof,
            }}
          >
            <div
              style={{
                width: 212 * cof,
                height: 40 * cof,
                borderRadius: 5,
                boxShadow: "0px 4px 4px 0px rgba(0, 0, 0, 0.03) inset",
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
                color: "#fff",
                fontSize: 16 * cof,
                fontWeight: 600,
                background: "#BA8545",
              }}
              onClick={addToCart}
            >
              Add to cart
            </div>
          </div>

          {/* <BottomBar windowWidth={windowWidth} indexType={2} /> */}
        </div>
      ) : (
        <div
          style={{
            width: "100%",
            height: "100vh",
            justifyContent: "center",
            alignItems: "center",
            display: "flex",
          }}
        >
          <LoaderBlack />
        </div>
      )}
    </>
  );
};

export default Product;
