import { useState } from "react";
import Accordion from "./Accordeon";
import chevrone from "../../assets/icons/chevrone.png";
const AccordionWrapper = ({ qaArray }) => {
  const [answers, setAnswers] = useState({});
  const [isPopupOpen, setIsPopupOpen] = useState(false);
  const [openMainAccordionIndex, setOpenMainAccordionIndex] = useState(null);

  const handleSendClick = () => {
    console.log(answers);
    setIsPopupOpen(true);
  };

  const handleClosePopup = () => {
    setIsPopupOpen(false);
  };
  const generateEmailBody = (answers) => {
    let text = "";
    Object.keys(answers).forEach((uniqueKey) => {
      Object.keys(answers[uniqueKey]).forEach((mainTitle) => {
        text += `${mainTitle}\n`; //   
        Object.keys(answers[uniqueKey][mainTitle]).forEach((groupTitle) => {
          text += `  ${groupTitle}\n`; //  
          Object.keys(answers[uniqueKey][mainTitle][groupTitle] || {}).forEach(
            (questionText) => {
              //    
              const answer =
                answers[uniqueKey][mainTitle][groupTitle][questionText];
              text += `    ${questionText}${
                answer === true ? "" : `: ${answer}`
              }\n`;
            }
          );
        });
      });
    });
    return text;
  };

  function sendEmail() {
    console.log(answers);
    const text = generateEmailBody(answers); //  answers    
    const subject = "  ";
    const body = encodeURIComponent(text);
    window.location.href = `mailto:example@example.com?subject=${subject}&body=${body}`;
  }
  return (
    <div>
      <div>
        {qaArray.map((qa, index) => (
          <div key={index}>
            <div
              style={{
                padding: "10px",
                marginTop: "5px",
                backgroundColor: "#f1f1f1",
                display: "flex",
                alignItems: "center",
                cursor: "pointer",
                fontWeight: "700",
              }}
              onClick={() =>
                setOpenMainAccordionIndex(
                  openMainAccordionIndex === index ? null : index
                )
              }
            >
              {qa.title}
              <img
                src={chevrone}
                style={{ width: 20, position: "absolute", right: 20 }}
              />
            </div>
            <div
              style={{
                overflow: "hidden",
                maxHeight: openMainAccordionIndex === index ? "5000px" : "0", // 
                transition: "max-height 0.3s ease",
              }}
            >
              <Accordion
                uniqueKey={`accordion-${index}`}
                title={qa.title}
                questions={qa.questions}
                setAnswers={setAnswers}
                test={true}
              />
            </div>
          </div>
        ))}
        <div
          style={{
            width: "100%",
            justifyContent: "center",
            display: "flex",
          }}
        >
          <div
            onClick={handleSendClick}
            style={{
              backgroundColor: "grey",
              padding: 10,
              borderRadius: 5,
              marginTop: 10,
              color: "#fff",
            }}
          >
            Send
          </div>
        </div>
      </div>
      {isPopupOpen && (
        <div
          style={{
            position: "fixed",
            top: "50%",
            left: "50%",
            transform: "translate(-50%, -50%)",
            backgroundColor: "white",
            boxShadow: "0px 0px 10px 0px rgba(0,0,0,0.75)",
            width: "90%",
            height: "90%",

            padding: 10,
          }}
        >
          <button onClick={handleClosePopup}>Close</button>
          <div style={{ height: "85%", overflow: "auto", marginTop: 10 }}>
            {Object.keys(answers).map((uniqueKey) => (
              <div key={uniqueKey}>
                {Object.keys(answers[uniqueKey]).map((mainTitle) => (
                  <div key={mainTitle}>
                    <div style={{ fontWeight: "700" }}>{mainTitle}</div>
                    {Object.keys(answers[uniqueKey][mainTitle]).map(
                      (groupTitle) => (
                        <div key={groupTitle}>
                          <div
                            style={{
                              marginTop: 5,
                              marginLeft: 10,
                              fontWeight: "600",
                            }}
                          >
                            {groupTitle}
                          </div>
                          {Object.keys(
                            answers[uniqueKey][mainTitle][groupTitle] || {}
                          ).map((questionText) => (
                            <div
                              key={questionText}
                              style={{ fontSize: 14, marginLeft: 10 }}
                            >
                              <div>
                                {questionText}
                                {answers[uniqueKey]?.[mainTitle]?.[
                                  groupTitle
                                ]?.[questionText] === true
                                  ? ""
                                  : `:    ${answers[uniqueKey]?.[mainTitle]?.[groupTitle]?.[questionText]}`}
                              </div>
                            </div>
                          ))}
                        </div>
                      )
                    )}
                  </div>
                ))}
              </div>
            ))}
          </div>
          <div
            style={{ width: "100%", justifyContent: "center", display: "flex" }}
          >
            <div
              style={{
                backgroundColor: "grey",
                padding: 10,
                borderRadius: 5,
                marginTop: 10,
                color: "#fff",
              }}
              onClick={sendEmail}
            >
              Send
            </div>
          </div>
        </div>
      )}
    </div>
  );
};
export default AccordionWrapper;
