import React, { useState } from "react";
import PayBanner from "./PayBanner";
import CheckBox from "./CheckBox";
import DeliveryInfo from "./DeliveryInfo";
import CardCont from "./CardCont";
import Address from "./Address";
import DeleveliDetails from "./DeleveliDetails";
import Popup from "./Popup";
import { Elements } from "@stripe/react-stripe-js";
import { loadStripe } from "@stripe/stripe-js";
import { useSelector } from "react-redux";
import CheckoutForm from "./CheckoutForm";
// const stripePromise = loadStripe(
//   "pk_test_51NAOFlELEDS8bBe4vvBoOFgGzj9pyZFhHAVQepVVMw01LDtyUeoz9VkefTHdzsFxC1VSBjtrIVyPZ3hSWegrOLoV00BMdyHu7B"
// );
const stripePromise = loadStripe(
  "pk_live_51Npx4aCQ1x9FQOi7sH4O9CyvxvbhYq7lDaQl2ZhReuySCE4DxPJycqJFv08jvNXYkUnkTM395969wT1A1ezNnOzI00yFRmEmWZ"
);
const PayInfo = ({ windowWidth }) => {
  const { price, clientSecret } = useSelector((state) => state.appReducer);
  const cof = windowWidth / 393;
  // const clientSecret =
  // ("pi_3NoUIhELEDS8bBe41RHZEZRy_secret_9Jxly3hD2ppJxqKNq9pHqXdF8");
  // const { clientSecret } = useSelector((state) => state.appReducer);
  // console.log(
  //   " ~ file: PayInfo.js:19 ~ PayInfo ~ clientSecret:",
  //   clientSecret
  // );
  const [popup, setPopup] = useState(false);

  const [dataCheckBox, setDataCheckbox] = useState([
    { active: false, type: "To the table" },
    { active: true, type: "Delivery" },
  ]);
  const [delivery, setDelivery] = useState([
    { active: false, title: "Delivery address", block: <Address cof={cof} /> },
    {
      active: false,
      title: "Delivery details",
      block: <DeleveliDetails cof={cof} />,
    },
    { active: true, title: "Credit card info", block: <CardCont cof={cof} /> },
  ]);
  const appearance = {
    theme: "stripe",
  };
  const options = {
    clientSecret,
    appearance,
  };
  const changeCheckbox = (index) => {
    let newArray = dataCheckBox.map((item) => {
      return { ...item, active: false };
    });
    newArray[index].active = true;
    setDataCheckbox(newArray);
  };
  const changeDelivery = (index) => {
    if (index !== 2) {
      let newArray = [...delivery];
      newArray[index].active = !newArray[index].active;
      setDelivery(newArray);
    }
  };
  return (
    <div>
      {popup && <Popup cof={cof} />}

      <div style={{ overflow: "scroll" }}>
        <div style={{ marginTop: 29 * cof }}>
          <PayBanner cof={cof} />
        </div>

        <CheckBox data={dataCheckBox} setData={changeCheckbox} cof={cof} />
        {/* <DeliveryInfo data={delivery} cof={cof} setData={changeDelivery} /> */}
      </div>
      {clientSecret && (
        // <div
        //   style={{
        //     width: "100%",
        //     height: "100vh",
        //     display: "flex",
        //     justifyContent: "center",
        //     alignItems: "center",
        //     zIndex: 99999999999999999,
        //     backgroundColor: "rgba(0, 0, 0, 0.8)",
        //     position: "absolute",
        //   }}
        // >
        <div
          style={{
            width: "90%",
            height: 400,
            marginTop: 40,
            backgroundColor: "#fff",
            marginLeft: "5%",
          }}
        >
          <Elements options={options} stripe={stripePromise}>
            <CheckoutForm total={price.total} />
          </Elements>
        </div>
        // </div>
      )}
      <div style={{ width: "100%", display: "flex", justifyContent: "center" }}>
        <div
          style={{
            width: 290 * cof,
            height: 50 * cof,
          }}
        ></div>
      </div>
    </div>
  );
};

export default PayInfo;
