import React, { useEffect, useState } from "react";
import logo from "../../assets/icons/logoText.png";
import arrow from "../../assets/icons/arrowRight.svg";
import stars from "../../assets/icons/alrRevStars.png";
import qa from "../../assets/icons/qa.png";
import write from "../../assets/icons/write.png";
import call from "../../assets/icons/call.png";
import { Link } from "react-router-dom";
import { useSelector } from "react-redux";
import ReactStars from "react-rating-stars-component";
import envirement from "../../assets/icons/envirement.png";
import eat from "../../assets/icons/eatAlt.png";
import drink from "../../assets/icons/drink.png";
import Accordion from "./Accordeon";
import AccordionWrapper from "./AccordionWrapper";
import { jsonQa } from "../../utils/jsonQa";

const AltReview = ({ windowWidth }) => {
  const cof = windowWidth / 393;
  const { slider1, slider2, slider3 } = useSelector(
    (state) => state.appReducer
  );
  const [showQa, setShowQa] = useState(false);
  const [showMessage, setShowMessage] = useState(false);
  const [textMessage, setTextMessage] = useState("");
  const [dataQa, setDataQa] = useState([]);
  useEffect(() => {
    const sliderValues = {
      slider1: (slider1 / 20).toFixed(0),
      slider2: (slider2 / 20).toFixed(0),
      slider3: (slider3 / 20).toFixed(0),
    };
    const filteredJsonQa = jsonQa.filter(
      (item) => sliderValues[item.value] <= 4
    );
    setDataQa(filteredJsonQa);
  }, [setDataQa]);

  function sendEmail() {
    //  answers    
    const subject = "Theme";
    const body = encodeURIComponent(textMessage);
    window.location.href = `mailto:example@example.com?subject=${subject}&body=${body}`;
  }

  return (
    <div>
      <div
        style={{
          display: "flex",
          alignItems: "center",
          marginLeft: 139 * cof,
          paddingTop: 27 * cof,
        }}
      >
        <img src={logo} style={{ width: 117 * cof }} />
        <Link style={{ marginLeft: 96 * cof }} to="/">
          <img src={arrow} style={{ width: 25 * cof }} />
        </Link>
      </div>
      <div style={{ textAlign: "center", marginTop: 14 * cof }}>
        Help Center
      </div>
      <div
        style={{
          width: 367 * cof,
          marginLeft: 13 * cof,
          fontSize: 14 * cof,
          fontWeight: 700,
          marginTop: 13 * cof,
        }}
      >
        We sincerely apologize for the negative experience you had during your
        visit to our restaurant.
        <br />
        Your feedback is crucial to us, and we're committed to improving and
        providing the best possible experience for our guests.
      </div>
      <div
        style={{
          width: 321 * cof,
          height: 1,
          background: "#000",
          marginLeft: 23 * cof,
          marginTop: 5 * cof,
        }}
      ></div>
      {(slider1 / 20).toFixed() < 4 && (
        <div
          style={{
            marginLeft: 25 * cof,
            marginTop: 10 * cof,
            display: "flex",
            alignItems: "center",
          }}
        >
          <img
            src={envirement}
            style={{ width: 30 * cof, paddingRight: 10 * cof }}
          />
          <ReactStars
            count={5}
            //   onChange={ratingChanged}
            size={14}
            activeColor="orange"
            value={(slider1 / 20).toFixed(0)}
          />
        </div>
      )}
      {(slider2 / 20).toFixed() < 4 && (
        <div
          style={{
            marginLeft: 25 * cof,
            marginTop: 10 * cof,
            display: "flex",
            alignItems: "center",
          }}
        >
          <img src={eat} style={{ width: 30 * cof, paddingRight: 10 * cof }} />
          <ReactStars
            count={5}
            //   onChange={ratingChanged}
            size={14}
            activeColor="orange"
            value={(slider2 / 20).toFixed(0)}
          />
        </div>
      )}
      {(slider3 / 20).toFixed() < 4 && (
        <div
          style={{
            marginLeft: 25 * cof,
            marginTop: 10 * cof,
            display: "flex",
            alignItems: "center",
          }}
        >
          <img
            src={drink}
            style={{ width: 30 * cof, paddingRight: 10 * cof }}
          />
          <ReactStars
            count={5}
            //   onChange={ratingChanged}
            size={14}
            activeColor="orange"
            value={(slider3 / 20).toFixed(0)}
          />
        </div>
      )}
      <div
        style={{
          width: 321 * cof,
          height: 1,
          background: "#000",
          marginLeft: 23 * cof,
          marginTop: 5 * cof,
        }}
      ></div>
      <div
        style={{
          width: 367 * cof,
          marginLeft: 13 * cof,
          fontSize: 14 * cof,
          fontWeight: 700,
          marginTop: 13 * cof,
        }}
      >
        We want to ensure your concerns are addressed promptly. How would you
        prefer to report a problem?
      </div>
      <div
        style={{ marginLeft: 27 * cof, marginTop: 15 * cof, display: "flex" }}
        onClick={() => setShowQa(!showQa)}
      >
        <img src={qa} style={{ width: 18 * cof, paddingRight: 10 * cof }} />
        <div
          style={{
            color: "#4E463C",
            fontSize: 14 * cof,
            fontWeight: 700,
          }}
        >
          You can use the QA method on this page.
        </div>
      </div>
      {showQa && <AccordionWrapper qaArray={dataQa} />}

      <div
        style={{ marginLeft: 27 * cof, marginTop: 10 * cof, display: "flex" }}
        onClick={() => setShowMessage(!showMessage)}
      >
        <img src={write} style={{ width: 18 * cof, paddingRight: 10 * cof }} />
        <div
          style={{
            color: "#4E463C",
            fontSize: 14 * cof,
            fontWeight: 700,
          }}
        >
          Write a cover letter.
        </div>
      </div>
      {showMessage && (
        <>
          <textarea
            style={{
              width: "88%",
              marginLeft: "5%",
              marginTop: 10,
              height: 200,
              borderRadius: 10,
              fontSize: 16,
              paddingTop: 10,
              paddingLeft: 10,
            }}
            onClick={(e) => setTextMessage(e.target.value)}
          />
          <div
            style={{
              width: "100%",
              justifyContent: "center",
              display: "flex",
            }}
          >
            <div
              style={{
                backgroundColor: "grey",
                padding: 10,
                borderRadius: 5,
                marginTop: 10,
                color: "#fff",
              }}
              onClick={sendEmail}
            >
              Send
            </div>
          </div>
        </>
      )}

      <div
        style={{ marginLeft: 27 * cof, marginTop: 10 * cof, display: "flex" }}
      >
        <img src={call} style={{ width: 18 * cof, paddingRight: 10 }} />
        <a
          style={{
            color: "#4E463C",
            fontSize: 14 * cof,
            fontWeight: 700,
          }}
          href="tel:+1234567890"
        >
          Call our General Manager office directly.
        </a>
      </div>
      <div
        style={{
          width: 367 * cof,
          marginLeft: 13 * cof,
          fontSize: 14 * cof,
          fontWeight: 700,
          marginTop: 13 * cof,
        }}
      >
        Alternatively, you can utilize all three methods, one by one, to ensure
        your feedback reaches us efficiently.
      </div>
      <div
        style={{
          display: "flex",
          width: 203 * cof,
          marginLeft: 95 * cof,
          marginTop: 67 * cof,
          justifyContent: "space-between",
          paddingBottom: 20 * cof,
        }}
      >
        <div
          style={{
            width: 45 * cof,
            height: 45 * cof,
            background: "#D9D9D9",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
          onClick={() => setShowQa(!showQa)}
        >
          <img
            src={qa}
            alt="qa"
            style={{ width: 28 * cof, height: 28 * cof }}
          />
        </div>
        <div
          style={{
            width: 45 * cof,
            height: 45 * cof,
            background: "#D9D9D9",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
          onClick={() => setShowMessage(!showMessage)}
        >
          <img
            src={write}
            alt="write"
            style={{ width: 28 * cof, height: 28 * cof }}
          />
        </div>
        <a
          style={{
            width: 45 * cof,
            height: 45 * cof,
            background: "#D9D9D9",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
          href="tel:+1234567890"
        >
          <img src={call} alt="call" style={{ width: 28 * cof }} />
        </a>
      </div>
    </div>
  );
};

export default AltReview;
