import React from "react";
import { states } from "../../utils/states";

const Select = ({ handleStateChange, selectedState, cof, placeHolder }) => {
  return (
    <div style={{ marginLeft: 34 * cof, marginTop: 15 * cof }}>
      <div style={{ fontSize: 16 * cof, fontWeight: "600" }}>{placeHolder}</div>
      <select
        value={selectedState.title}
        onChange={handleStateChange}
        style={{
          width: 325 * cof,
          height: 50 * cof,
          borderRadius: 5,
          border:
            "1px solid var(--divider-light-1-px-60, rgba(228, 228, 228, 0.60))",
          background: "#fff",
          marginTop: 15 * cof,
          outline: "none",
          fontSize: 16,
        }}
      >
        <option value="">Select a state</option>
        {states.map((state) => (
          <option key={state.code} value={state.code}>
            {state.title}
          </option>
        ))}
      </select>
    </div>
  );
};

export default Select;
