import React, { useState, useRef } from "react";
import chevrone from "../../assets/icons/chevrone.png";

//  
const QuestionItem = ({ text, isLast, title, mainTitle, setAnswers }) => {
  const [checked, setChecked] = useState(false);
  const [inputValue, setInputValue] = useState("");

  const handleInputChange = (e) => {
    setInputValue(e.target.value);
    setAnswers(title, text, e.target.value); //  title  text   
  };

  const handleCheckboxChange = () => {
    setChecked(!checked);
    setAnswers(title, text, !checked); //  title  text   
  };

  if (isLast) {
    return (
      <div
        style={{
          padding: "10px",
          marginTop: "5px",
          backgroundColor: "#f1f1f1",
          display: "flex",
          flexDirection: "column",
          width: "90%",
          marginLeft: "3%",
        }}
      >
        <textarea
          placeholder={text}
          value={inputValue}
          onChange={handleInputChange}
          style={{ resize: "none", width: "100%", maxHeight: "200px" }}
          maxLength={1000}
        />
      </div>
    );
  }

  return (
    <div
      style={{
        padding: "10px",
        marginTop: "5px",
        backgroundColor: "#f1f1f1",
        display: "flex",
        alignItems: "center",
        width: "90%",
        marginLeft: "3%",
      }}
    >
      <input
        type="checkbox"
        checked={checked}
        onChange={handleCheckboxChange}
      />
      <span style={{ marginLeft: "10px" }}>{text}</span>
    </div>
  );
};

//   
const QuestionGroup = ({
  title,
  items,
  isOpen,
  onClick,
  setAnswers,
  mainTitle,
}) => {
  return (
    <div>
      <div
        style={{
          padding: "10px",
          marginTop: "5px",
          backgroundColor: "#f1f1f1",
          display: "flex",
          alignItems: "center",
          cursor: "pointer",
          fontWeight: "500",
          position: "relative",
          width: "90%",
          marginLeft: "3%",
        }}
        onClick={onClick}
      >
        {title}
        <img
          src={chevrone}
          style={{ width: 20, position: "absolute", right: 20 }}
        />
      </div>
      <div
        style={{
          overflow: "hidden",
          maxHeight: isOpen ? "1000px" : "0",
          transition: "max-height 0.3s ease",
        }}
      >
        {items.map((item, index) => (
          <QuestionItem
            key={index}
            {...item}
            title={title}
            isLast={index === items.length - 1}
            setAnswers={setAnswers}
            mainTitle={mainTitle}
          />
        ))}
      </div>
    </div>
  );
};

//   
const Accordion = ({ title, questions, setAnswers, uniqueKey, test }) => {
  const [openIndex, setOpenIndex] = useState(null);

  const handleSetAnswers = (groupTitle, text, value) => {
    setAnswers((prev) => ({
      ...prev,
      [uniqueKey]: {
        ...prev[uniqueKey],
        [title]: {
          ...prev[uniqueKey]?.[title],
          [groupTitle]: {
            ...prev[uniqueKey]?.[title]?.[groupTitle],
            [text]: value,
          },
        },
      },
    }));
  };

  return (
    <div
      style={{
        fontFamily: "Arial, sans-serif",
        marginTop: 10,
        paddingBottom: 10,
      }}
    >
      {questions.map((question, index) => (
        <QuestionGroup
          key={index}
          {...question}
          isOpen={index === openIndex}
          onClick={() => setOpenIndex(index === openIndex ? null : index)}
          setAnswers={handleSetAnswers}
          mainTitle={title}
        />
      ))}
    </div>
  );
};

export default Accordion;
