import axios from "axios";
import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import { states } from "../../utils/states";
import { useDispatch } from "react-redux";
import { changePrice, setClientSecret } from "../../redux/appReducer";
import WhiteLoader from "../../utils/WhiteLoader";

const Next = ({ cof, inputData }) => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const [loader, setLoader] = useState(false);
  const send = async () => {
    setLoader(true);
    const axiosConfig = {
      headers: {
        Authorization: `Bearer A5tPuY7OMklHgY6cCVOZYHCZbpViehKusA9ITEcH`,
      },
    };

    try {
      let items = [];
      const filterState = states.filter((el) => el.code === inputData.state);
      console.log(" ~ file: Next.js:16 ~ send ~ filterState:", filterState);

      const cartString = localStorage.getItem("cartData");
      const cart = JSON.parse(cartString);
      for (let itemFirst of cart) {
        let i = 1;
        while (i <= itemFirst.count) {
          for (let itemSecond of itemFirst.variants) {
            delete itemSecond.external_id;
            if (itemSecond.title === itemFirst.size) {
              items.push(itemSecond);
            }
          }
          i++;
        }
      }
      const postObject = items.map((i) => {
        return {
          variant_id: i.variant_id,
          quantity: 1,
          files: [
            {
              url: i.files[0].preview_url,
            },
          ],
        };
      });
      console.log(
        " ~ file: Next.js:48 ~ postObject ~ postObject:",
        postObject
      );
      console.log("items", items);
      const data = {
        shipping: "STANDARD",
        recipient: {
          name: inputData.name,
          address1: inputData.street,
          city: inputData.city,
          state_code: filterState[0].code,
          state_name: filterState[0].title,
          country_code: "US",
          country_name: "United States",
          zip: inputData.zip,
          phone: inputData.phone,
          email: inputData.email,
        },
        items: postObject,
      };

      const dataPrice = await axios.post(
        "https://stingray-app-lodnw.ondigitalocean.app/https://api.printful.com/orders",
        data,
        axiosConfig
      );
      console.log(" ~ file: Next.js:75 ~ send ~ dataPrice:", dataPrice);
      const result = dataPrice.data.result.costs;
      console.log(" ~ file: Next.js:76 ~ send ~ result:", result);
      const stripePaymentRequest = await axios.post(
        `https://stingray-app-lodnw.ondigitalocean.app/http://ext.pixomnia.com:8087/payments/createPaymentIntent`,
        {
          requestJson: JSON.stringify(data),
          responseJson: JSON.stringify(dataPrice.data),
          totalItems: postObject.length,
          totalCost: result.total * 100,
          draftId: String(dataPrice.data.result.id),
          placeId: "tikis",
        }
      );
      if (stripePaymentRequest) {
        setLoader(false);
      }
      dispatch(setClientSecret(stripePaymentRequest.data.clientSecret));
      console.log(
        " ~ file: Next.js:88 ~ send ~ stripePaymentRequest:",
        stripePaymentRequest
      );
      dispatch(
        changePrice({
          total: result.total,
          subTotal: result.subtotal,
          shipping: result.shipping,
        })
      );
      navigate("/pay_info");
    } catch (err) {
      console.log(err);
    }
  };
  return (
    <div style={{ width: "100%", display: "flex", justifyContent: "center" }}>
      <div
        style={{
          width: 290 * cof,
          height: 50 * cof,
          background: "#BA8545",
          color: "#fff",
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          borderRadius: 5,
          cursor: "pointer",
        }}
        to="/pay_info"
        onClick={send}
      >
        {loader ? <WhiteLoader /> : "Next"}
      </div>
    </div>
  );
};

export default Next;
